<template>
 <div class="course-construction">
     <title-curmb></title-curmb>
     <course-steps :title="courseName" :step="3"></course-steps>
     <div class="course-construction_body">
         <el-form :model="form" :rules="rules" ref="form" label-width="0">
            <h2>教学内容与基本要求</h2>
            <el-row>
                <el-form-item  prop="tableData">
                    <el-table :data="tableData" border class="cc-table" style="width: 100%;">
                        <el-table-column type="index" label="序号" align="center">
                        </el-table-column>
                        <el-table-column label="教学内容（知识点）">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.courseContent"  type="textarea" :rows="1" ></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="教学要求">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.courseRequirment"  type="textarea" :rows="1" ></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="学时" width="200">
                            <template slot-scope="scope">
                                <el-input-number v-model="scope.row.period"  :min="0" controls-position="right"></el-input-number>
                            </template>
                        </el-table-column>
                        <el-table-column label="教学方式" width="200">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.courseMode"></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="课程目标" width="210">
                            <template slot-scope="scope">
                                <el-select v-model="scope.row.checkedObjective"  filterable  multiple placeholder="请选择" class="tail-end-input">
                                    <el-option
                                    v-for="item in programObjective"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id"
                                    ></el-option>
                                </el-select>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作" width="50">
                            <template slot-scope="scope">
                                <label @click="basicDel(scope.row, scope.$index)" class="tail-end_del"><SvgIcon icon-class="iconqudiao" size="20px"></SvgIcon></label>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
                <p class="b-remark">备注：①教学内容为对应支撑课程教学目标指标点的教学内容（知识点）；②教学要求为针对课程教学目标提出的具体要求；③教学方式包括面授和线上，其中面授包括：讲授、实验、讨论。</p>
                <div @click="addProgramObjective" class="add-step-title add-step-button"><SvgIcon icon-class="icontianjia" size="20px"></SvgIcon><span>添加教学内容与基本要求</span></div>
            </el-row>
            <el-row :gutter="24">
                <el-col :span="6" :offset="9">
                    <div class="c-footer">
                        <el-button @click="formSubmit('form', 'save')">保&nbsp;&nbsp;存</el-button>
                        <el-button type="primary" @click="formSubmit('form', 'next')">下一步</el-button>
                    </div>
                </el-col>
            </el-row>
       </el-form>
     </div>
 </div>
</template>

<script>
import TitleCurmb from './components/TitleCurmb'
import CourseSteps from './components/CourseSteps'

export default {
 name: 'BasicRequirements',
 components: {TitleCurmb, CourseSteps},
 data() {
    const checkTableData = (rule, value, callback) => {   
        let num=0
        if(this.tableData.length ===0){
            callback(new Error('请添加课程目标'))
        }
        this.tableData.forEach(vo =>{
            if(vo.courseContent==='' || vo.courseRequirment === '' || vo.period === '' || vo.period === 0 || vo.courseMode === '' || vo.checkedObjective.length ===0){
                num ++
            }
        })
        if(num ===0){
           callback()
        }else{
           callback(new Error('教学内容与基本要求信息不能有空'))
        }
    };
 return {
    courseName: this.$route.query.title,
    programObjective:[],
    tableData:[],
    tableDataDelArr:[],
    form:{
        tableData:[]
    },
    rules:{
        tableData: [{ required: true, validator: checkTableData, trigger: "blur"}], 
    },
    api:{
         details:courseId=>`/basic/course/setContentView/${courseId}`, //详情
         getId:() =>`/basic/courseGoal/getId`, //获取动态ID
         courseGoal: courseId =>`/basic/courseGoal/course/${courseId}`, //课程目标
         save:() => `/basic/course/setContent`
    }
 }
 },
 created(){},
 mounted(){
     this.getCourseGoal()
     this.initData()
 },
 methods:{
     initData(){
        const url=this.api.details(this.$route.query.id)
        this.$get(url).then(res =>{
            if(res.status ===1){     
                const arr= res.data || [];
               arr.forEach(item =>{
                   item.changed=1
                   item.checkedObjective=[]
                   item.contentGolds.forEach(vo =>{
                        item.checkedObjective.push(vo.courseGoalId)
                   })
               })
               this.tableData=arr;
            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
     getCourseGoal(){
        const url=this.api.courseGoal(this.$route.query.id)
        this.$get(url).then(res =>{
             if(res.status ===1){
                this.programObjective=res.data;
             }else{
                 this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
             }
         })
     },
    addProgramObjective(){
         this.$get(this.api.getId()).then(res => {
            const obj={
                courseContent:'',
                courseRequirment:'',
                period:'',
                courseMode:'',
                checkedObjective:[],
                changed:0,
                courseId:this.$route.query.id,
                id: res
              }
            this.tableData.push(obj)
           
        });
    },
    basicDel(row, index){
       if(row.changed ===1){
            row.changed=2;
            this.tableDataDelArr.push(row);
        }

        this.tableData.splice(index, 1);
      
    },
    formSubmit(formName, type){
         if(type === 'save'){
             this.$refs[formName].resetFields()
             this.userSave('save');
         }else{ 
            this.$refs[formName].validate(valid => {
                if (valid) {
                     this.userSave('next');
                }
            });
         }
       
     },
      userSave(type){
      const arr=JSON.parse(JSON.stringify(this.tableData)).concat(this.tableDataDelArr)
      const list=arr.map(item =>{
           const obj={
               courseId:item.courseId,
               courseContent:item.courseContent,
               courseRequirment:item.courseRequirment,
               period:item.period,
               id:item.id,
               courseMode:item.courseMode,
               changed:item.changed,
               contentGolds:[]
           }
           item.contentGolds=[]
           item.checkedObjective.forEach(vo =>{
               obj.contentGolds.push({
                  courseContentId:item.id,
                  courseGoalId:vo
              })
             
          })
           
           return obj

      })
      const postData={
          id:this.$route.query.id,
          courseContents:list
      }

      this.$post(this.api.save(), postData).then( res => {
          if(res.status === 1){
              this.$notify.success({
                  title: 'success',
                  duration:1500,
                  message: res.message
                  })
              if(type === 'next'){
                    this.$router.push({
                      path:'/Construction/appraisalMethod',
                      query:{
                          id:this.$route.query.id,
                          title:this.courseName
                      }
                  })
              }else{
                  this.initData()
              } 
          }else{
              this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.message
              })
          }  
      })
     }
 }
}
</script>

<style lang='scss' scoped>
 @import '../../styles/courseConstruction.scss';
</style>



